import { useDisclosure, useToast, Box, Button, Tooltip, Text, Menu, MenuButton, MenuList, Avatar, MenuItem, MenuDivider, Drawer, DrawerOverlay, DrawerContent, DrawerHeader, DrawerBody, Input, Spinner } from '@chakra-ui/react'
import { BellIcon, ChevronDownIcon } from '@chakra-ui/icons'
import React,{useState} from 'react'
import { ChatState } from '../../context/ChatProvider'
import ProfileModal from './ProfileModal'
import { useNavigate } from 'react-router-dom'
import axios from "axios";
import ChatLoading from '../ChatLoading'
import UserListItem from '../UserAvatar/UserListItem'
import { getSender } from '../../config/ChatLogics'
import {Effect} from 'react-notification-badge'
import NotificationBadge from "react-notification-badge";

const SideDrawer = () => {
    const [search, setSearch] = useState("")
    const [searchResult, setSearchResult] = useState([])
    const [loading, setLoading] = useState(false)
    const [loadingChat, setLoadingChat] = useState()

    const { user, setSelectedChat, chats, setChats, notification, setNotification } = ChatState();
    const navigate = useNavigate()
    const {onClose, isOpen, onOpen} = useDisclosure()
    const toasts = useToast()
    const logoutHandler = () => {
        localStorage.removeItem('userInfo')
        navigate('/')
    }
    const handleSearch = async () => {
        if(!search){
            toasts({
                title: "Please Enter something in search",
                status: "warning",
                duration: 5000,
                isClosable: true,
                position: "top-left",
              });
              return;
            }
        
            try {
              setLoading(true);
        
              const config = {
                headers: {
                  Authorization: `Bearer ${user.token}`,
                },
              };
        
              const { data } = await axios.get(`/api/user?search=${search}`, config);
        
              setLoading(false);
              setSearchResult(data);
            } catch (error) {
              toasts({
                title: "Error Occured!",
                description: "Failed to Load the Search Results",
                status: "error",
                duration: 5000,
                isClosable: true,
                position: "bottom-left",
              });
            }
          };
        

const accessChat = async (userId) => {

    try {
        setLoadingChat(true);
        const config = {
        headers: {
            "Content-type": "application/json",
            Authorization: `Bearer ${user.token}`,
        },
        };
        const { data } = await axios.post(`/api/chat`, { userId }, config);

        if (!chats.find((c) => c._id === data._id)) setChats([data, ...chats]);
        setSelectedChat(data);
        setLoadingChat(false);
        onClose();
    } catch (error) {
        toasts({
        title: "Error fetching the chat",
        description: error.message,
        status: "error",
        duration: 5000,
        isClosable: true,
        position: "bottom-left",
        });
    }
    };

    
  return (
    <div>
        <Box
            display="flex"
            justifyContent="space-between"
            alignItems="center"
            bg="white"
            w="100%"
            p="5px 10px 5px 10px"
            borderWidth="5px">
            <Tooltip label="Search Users to chat" hasArrow placement="bottom-end">
                <Button variant="ghost" onClick={onOpen}>
                    <i className="fas fa-search"></i>
                    <Text d={{base:"none",md:"flex"}} px="4">Search user</Text>
                </Button>
            </Tooltip>
            <Text fontSize="2xl" fontFamily="Work sans">
                Yaari
            </Text>
            <div>
                <Menu>
                    <MenuButton p={1}>
                        <NotificationBadge count={notification.length} effect={Effect.SCALE}/>
                        <BellIcon fontSize="2xl" m={1}/>
                    </MenuButton>
                    <MenuList pl={2} pt={1}>
                        {!notification.length && "No new Messages"}
                        {notification.map((n)=>(
                            <MenuItem key={n._id} onClick={()=>{
                                setSelectedChat(n.chat)
                                setNotification(notification.filter((not)=>not._id !== n._id))
                            }}>
                                {n.chat.isGroupChat ? `New Message in ${n.chat.chatName}`: `New Message from ${getSender(user, n.chat.users)}`}
                            </MenuItem>
                        ))}
                    </MenuList>
                </Menu>
                <Menu>
                    <MenuButton as={Button} rightIcon={<ChevronDownIcon/>}>
                        <Avatar size="sm" cursor="pointer" name={user.name} src={user.pic}/>
                    </MenuButton>
                    <MenuList>
                        <ProfileModal user={user}>
                        <MenuItem>My Profile</MenuItem>
                        </ProfileModal>
                        <MenuDivider/>
                        <MenuItem onClick={logoutHandler}>Logout</MenuItem>
                    </MenuList>
                </Menu>
            </div>
        </Box>
        <Drawer placement='left' onClose={onClose} isOpen={isOpen}>
            <DrawerOverlay/>
            <DrawerContent>
                <DrawerHeader borderBottomWidth="1px">Search User</DrawerHeader>
                <DrawerBody>
                    <Box display="flex"
                        pb={2}
                        >
                            <Input
                                placeholder="Search user"
                                value={search}
                                onChange={(e)=>setSearch(e.target.value)}
                                mr={2}
                            />
                            <Button onClick={handleSearch}>Go</Button>

                        </Box>
                        {loading ? ( <ChatLoading/>) : (
                            searchResult.map((user)=>(
                                <UserListItem key={user._id} user={user} handleFunction={()=>accessChat(user._id)}/>
                            ))
                        )}
                        {loadingChat && <Spinner ml="auto" display="flex" />}
                </DrawerBody>
            </DrawerContent>
            
        </Drawer>
    </div>
  )
}

export default SideDrawer